// FooterElements.scss

.FooterContainer {
    background-color: #101522;
}

.FooterWrap {
    padding: 48px 24px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    max-width: 1100px;
    margin: 0 auto;
}

.FooterLinksContainer {
    display: flex;
    justify-content: center;

    @media screen and (max-width: 820px) {
        padding-top: 32px;
    }
}

.FooterLinksWrapper {
    display: flex;

    @media screen and (max-width: 820px) {
        flex-direction: column;
    }
}

.FooterLinkItems {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    margin: 16px;
    text-align: left;
    width: 160px;
    box-sizing: border-box;
    color: #fff;

    @media screen and (max-width: 420px) {
        margin: 0;
        padding: 10px;
        width: 100%;
    }
}

.FooterLinkTitle {
    font-size: 14px;
    margin-bottom: 16px;
}

.FooterLink {
    color: #fff;
    text-decoration: none;
    margin-bottom: 0.5rem;
    font-size: 14px;

    &:hover {
        color: #ff8368;
        transition: 0.3s ease-out;
    }
}

.SocialMedia {
    max-width: 1000px;
    width: 100%;
}

.SocialMediaWrap {
    display: flex;
    justify-content: space-between;
    align-items: center;
    max-width: 1100px;
    margin: 40px auto 0 auto;

    @media screen and (max-width: 820px) {
        flex-direction: column;
    }
}

.SocialLogo {
    color: #fff;
    justify-self: start;
    cursor: pointer;
    text-decoration: none;
    font-size: 1.5rem;
    display: flex;
    align-items: center;
    margin-bottom: 16px;
    font-weight: bold;
}

.WebsiteRights {
    color: #fff;
    margin-bottom: 16px;
}

.SocialIcons {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 240px;
}

.SocialIconLink {
    color: #fff;
    font-size: 24px;
}